
import { defineComponent } from 'vue';
import { gsap } from 'gsap';
import { Draggable } from 'gsap/Draggable';
import Level from '@/services/Level';

gsap.registerPlugin(Draggable);

export default defineComponent({
  name: 'MainGame',
  data() {
    return {
      field: this.$refs['game-field'] as HTMLElement,
      columns: 9,
      rows: 9,
      level: new Level(9, 9),
      pieceWidth: 0
    };
  },
  computed: {
    divWidth(): number {
      return 100 / this.columns;
    },
    divHeight(): number {
      return 100 / this.rows;
    },
    pieces(): number {
      return this.columns * this.rows;
    }
  },
  methods: {},
  mounted() {
    this.level.generate();
    const items = document.querySelectorAll('.piece');

    Draggable.create('.piece', {
      type: 'x,y',
      edgeResistance: 1,
      lockAxis: true,
      cursor: 'grab',
      onPress: function () {
        Draggable.get(this.target).applyBounds({
          minX: -this.target.offsetWidth,
          minY: -this.target.offsetHeight,
          maxX: this.target.offsetWidth,
          maxY: this.target.offsetHeight
        });
        this.startX = this.x;
        this.startY = this.y;
      },
      onDrag: function () {
        let i = items.length;
        while (--i > -1) {
          if (this.hitTest(items[i], 60)) {
            console.log('hit');
          }
        }
      },
      onDragEnd: function () {
        const target = this.target;
        Draggable.get(target).disable();
        gsap.to(target, {
          duration: 0.3,
          x: this.startX,
          y: this.startY,
          ease: 'bounce.out',
          onComplete: function () {
            Draggable.get(target).enable();
          }
        });
      }
    });
  }
});
